@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
.App {
  font-family: sans-serif;
  text-align: center;
  height: 100vh;
}

body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  height: 100%;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #16a085;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.editor {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.editorInput {
  flex: 0.5 1;
  height: 100%;
  position: relative;
}
.colorSelectionDiv {
  position: absolute;
  top: -35px;
  left: 15px;
  border-radius: 5px;
  background-color: white;
  color: #8e44ad;
  height: 25px;
  width: 100px;
  font-size: 15px;
  cursor: pointer;
  box-shadow: inset 1px 2px 5px #1abc9c;
}

.editorInputArea {
  width: 100%;
  height: 100%;
  /* background-color: #273746;
  color: #58d68d; */
  border-right: 5px solid #b2babb;
  font-size: 25px;
  padding-left: 20px;
  padding-top: 20px;
  box-sizing: border-box;
}

.editorOutput {
  flex: 0.5 1;
  height: 100%;
  background-color: #f2f4f4;
  overflow-x: hidden;
  word-break: break-all;
  text-align: left;
  padding-left: 20px;
  box-sizing: border-box;
  position: relative;
}

.outputSmall {
  position: absolute;
  top: 0px;
  right: 10px;
  color: #117a65;
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.editorOutput img {
  width: 90%;
}

@media screen and (max-width: 780px) {
  .editor {
    flex-direction: column;
  }
  .editorInputArea {
    font-size: 15px;
  }

  .colorSelectionDiv {
    height: 25px;
    width: 50px;
    font-size: 10px;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;
  border-radius: 10px;
  width: 400px;
  height: auto;
  top: 20%;
  left: 30%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.modal_heading {
  background-color: #2ecc71;
  padding: 10px 10px;
  font-weight: 800;
  color: white;
  letter-spacing: 1px;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
}

.modal_part1,
.modal_part2,
.modal_part3 {
  padding: 15px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
}

.modal_part1,
.modal_part2 {
  box-shadow: 1px 1px 3px gray;
}

.fontTextbox {
  width: 20%;
  border: none;
  height: 20px;
  box-shadow: inset 1px 2px 3px gray;
}

.fontTextbox:focus {
  outline: none;
  background-color: #e8f8f5;
}

.modalClose_button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.modalClose_button:hover {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  transition: 0.3s;
}

/* .button__div {
  display: flex;
  justify-content: space-around;
} */

.apply__button {
  background-color: #48c9b0;
  box-shadow: 1px 3px 7px #808b96;
  width: 30%;
  border: none;
  border-radius: 20px;
  color: white;
  padding: 8px 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-top: 15px;
  cursor: pointer;
  font-weight: 700;
  letter-spacing: 1.5px;
}

@media screen and (max-width: 780px) {
  .modal-main {
    width: 90%;
    top: 30%;
    left: 50%;
  }
}

.headerh1 {
  background-color: #17202a;
  margin-top: 0;
  margin-bottom: 0;
  padding: 10px 0px;
  color: white;
  position: relative;
  font-size: 25px;
}

.colorPicker {
  position: absolute;
  top: 10px;
  left: 20px;
  border: 1px solid green;
  padding: 0px 0px;
  border-radius: 5px;
}

@media screen and (max-width: 780px) {
  .headerh1 {
    font-size: 20px;
  }
}

